import React from 'react'

interface IOptions {
  confirmDisableMs: number
  resetToInitialMs: number
  onConfirm: () => void
  ref: React.MutableRefObject<{ disabled: boolean } | null>
}

export function useConfirmWithTimeout({
  confirmDisableMs,
  resetToInitialMs,
  onConfirm,
  ref,
}: IOptions) {
  const [confirming, setConfirming] = React.useState(false)
  const showConfirm = React.useCallback(() => {
    if (ref.current) {
      ref.current.disabled = true
    }
    setConfirming(true)

    setTimeout(() => {
      if (ref.current) {
        ref.current.disabled = false
      }
    }, confirmDisableMs)
  }, [ref, confirmDisableMs])

  const confirm = React.useCallback(() => {
    setConfirming(false)
    onConfirm()
  }, [onConfirm])

  const onClick = confirming ? confirm : showConfirm

  React.useEffect(() => {
    if (confirming) {
      const timer = setTimeout(() => setConfirming(false), resetToInitialMs)
      return () => clearTimeout(timer)
    }
    return undefined
  }, [resetToInitialMs, confirming])

  return { confirming, onClick }
}
