import { SECOND } from '@speedlo/hooks'
import { cn } from '@speedlo/ui'
import React from 'react'
import styled from 'styled-components'

import { Sizer } from '../atoms/Sizer'
import { useConfirmWithTimeout } from '../hooks/useConfirmWithTimeout'
import { Colors, Spacing } from '../styling'
import { TouchButton } from './TouchButton'

interface IProps {
  confirmNode: ReactNode | string
  selectNode: ReactNode | string
  initialColor?: Colors
  confirmColor?: Colors
  confirmDisableMs?: number
  resetToInitialMs?: number
  disabled?: boolean
  onConfirm(): void
}

const StyledSelectButton = styled.div`
  display: grid;
  grid-auto-flow: column;
  grid-gap: 5px;

  > * {
    height: 100%;
  }

  button.flat-left {
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
  }
`

export const SelectButton: React.FC<IProps> = ({
  confirmNode,
  selectNode,
  initialColor = Colors.greyMiddle,
  confirmColor = Colors.red,
  confirmDisableMs = 300,
  resetToInitialMs = 30 * SECOND,
  disabled,
  children,
  onConfirm,
}) => {
  const ref = React.useRef<HTMLButtonElement | null>(null)
  const { confirming, onClick } = useConfirmWithTimeout({
    confirmDisableMs,
    resetToInitialMs,
    onConfirm,
    ref,
  })

  return (
    <StyledSelectButton>
      {confirming && selectNode}
      <Sizer width={Spacing.OrderFlowMenuButtonWidth}>
        <TouchButton
          ref={ref}
          onClick={onClick}
          color={confirming ? confirmColor : initialColor}
          className={cn({ 'flat-left': confirming })}
          disabled={disabled}
        >
          {confirming ? confirmNode : children}
        </TouchButton>
      </Sizer>
    </StyledSelectButton>
  )
}
