import { idProp } from '@speedlo/tools'
import React from 'react'
import { Props } from 'react-select/lib/Select'

import { SelectControl } from '../../../atoms/SelectControl'
import { FPaymentMethodsPaymentMethods as FPaymentMethod } from '../../../graph/generated'
import { Radiuses } from '../../../styling'

interface IProps extends Props<FPaymentMethod> {
  paymentMethods: RoA<FPaymentMethod>
  selectedMethod: Maybe<FPaymentMethod>
  onSelection: (method: Maybe<FPaymentMethod>) => void
}

export const PaymentMethodDropdown: React.FC<IProps> = ({
  paymentMethods,
  selectedMethod,
  onSelection,
  ...props
}) => (
  <SelectControl<FPaymentMethod>
    value={selectedMethod}
    options={paymentMethods}
    getOptionValue={idProp}
    isSearchable={false}
    formatOptionLabel={method => method.description || method.paymentGate.code}
    filterOption={(option, rawInput) => {
      const { description, paymentGate } = option.data as FPaymentMethod
      const searchedText = description || paymentGate.code

      return searchedText
        .toLocaleLowerCase()
        .includes(rawInput.toLocaleLowerCase())
    }}
    styles={{
      control: base => ({
        ...base,
        borderRadius: Radiuses.Button,
        borderTopRightRadius: 0,
        borderBottomRightRadius: 0,
        height: '100%',
        margin: 0,
      }),
    }}
    onChange={selection => {
      onSelection((selection as FPaymentMethod) || null)
    }}
    {...props}
  />
)
