import { SECOND } from '@speedlo/hooks'
import React from 'react'

import { useConfirmWithTimeout } from '../hooks/useConfirmWithTimeout'
import { Colors } from '../styling'
import { TouchButton } from './TouchButton'

interface IProps {
  confirmNode: ReactNode | string
  initialColor?: Colors
  confirmColor?: Colors
  confirmDisableMs?: number
  resetToInitialMs?: number
  disabled?: boolean
  onConfirm(): void
}

export const ConfirmButton: React.FC<IProps> = ({
  confirmNode,
  initialColor = Colors.greyMiddle,
  confirmColor = Colors.red,
  confirmDisableMs = 300,
  resetToInitialMs = 3 * SECOND,
  disabled,
  children,
  onConfirm,
}) => {
  const ref = React.useRef<HTMLButtonElement | null>(null)
  const { confirming, onClick } = useConfirmWithTimeout({
    confirmDisableMs,
    resetToInitialMs,
    onConfirm,
    ref,
  })

  return (
    <TouchButton
      ref={ref}
      onClick={onClick}
      color={confirming ? confirmColor : initialColor}
      disabled={disabled}
    >
      {confirming ? confirmNode : children}
    </TouchButton>
  )
}
